@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.welcome {
  width: 100vw;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 50vh;
}

.welcome p {
  margin: 5px;
}

@media screen and (max-width: 500px) {
  .welcome {
    margin-top: 50px;
  }

  .welcome > p {
    padding: 0 40px;
    text-align: center;
  }
}

.input {
  width: 100%;
  height: 20px;
  margin-top: 5px;
  border: none;
  font-size: 15px;
}

#styled-input {
  position: absolute;
  display: flex;
  height: 35px;
  width: 335px;
  box-sizing: border-box;
  border: 1px solid rgb(214, 211, 211);
  border-radius: 7px;
  background-color: white;
  color: black;
}

#styled-input img {
  height: 20px;
  padding: 5px 10px;
}

#suggestion-container {
  max-height: 300px;
  width: 335px;
  margin-top: 25px;
  border-radius: 7px;
  overflow: hidden;
  overflow-y: scroll;
  background-color: white;
  color: black;
}

ul {
  list-style-type: none;
  margin-top: 30px;
  padding: 10px 0;
}

ul > p {
  padding: 5px 10px;
  margin: 0;
  border-radius: 5px;
}

ul > p:hover {
  background-color: rgb(243, 243, 243);
}

::-webkit-input-placeholder {
  color: black;
  font-size: 12px;
}

:-ms-input-placeholder {
  color: black;
  font-size: 12px;
}

::-ms-input-placeholder {
  color: black;
  font-size: 12px;
}

::placeholder {
  color: black;
  font-size: 12px;
}

*:focus {
  outline: none;
}

.city-card {
  width: 300px;
  display: flex;
  flex-direction: column;
  height: auto;
  color: black;
  background-color: white;
  border-radius: 10px;
  margin: 20px;
  padding: 20px;
}

h3,
p {
  margin: 2px;
}

#date {
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 600;
}
#location {
  color: #693aaa;
  font-weight: 800;
}
#city {
  font-size: 10px;
}
#values-container {
  display: flex;
  font-weight: 600;
  font-size: 12px;
}
#value {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 12px;
}

#close {
  display: flex;
  justify-content: flex-end;
}

#close-icon {
  height: 20px;
  position: absolute;
}

body {
  background-image: -webkit-gradient(linear, left top, right top, from(#693aaa), to(#5778ab));
  background-image: linear-gradient(to right, #693aaa, #5778ab);
  color: white;
  font-family: "Open Sans", sans-serif;
}

.card-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

